.badge, .badge--unread {
  border-radius: 1.7em;
  display: inline-block;
  font-size: 0.8em;
  font-weight: normal;
  height: 1.5em;
  line-height: 1.5em;
  margin: 0 0.5em;
  min-width: 1.5em;
  vertical-align: middle;
  white-space: nowrap;
}

.badge--unread {
  background-color: #E32636;
  color: #EFEFEF;
  pointer-events: none;
}

.button__search {
  font-size: 0.75rem;
  margin-left: -2.5em;
}

.form__search {
  align-items: inherit;
  display: inherit;
  flex: 1 1;
  justify-content: flex-end;
}

.form__message {
  padding: 1.5em;
}

.form__message textarea {
  border: 0.1em solid #BFC1C2;
  border-radius: 0.25em;
  outline: none;
  resize: none;
  width: 100%;
}

.statement-box {
  padding: 1.5em;
}

.statement-box textarea {
  border: 0.1em solid #BFC1C2;
  border-radius: 0.25em;
  outline: none;
  resize: none;
  width: 100%;
}

.image__avatar, .image__avatar--dashboard, .image__avatar--chat {
  border-radius: 25%;
}

.image__avatar--dashboard {
  margin: 1em 0.75em 0 1em;
  max-height: 4em;
  max-width: 4em;
}

.image__avatar--chat {
  max-height: 2em;
  max-width: 2em;
}

.image--gif {
  float: left;
  margin: 0.5em 0 0.5em 0.75em;
  max-height: 15em;
  max-width: 20em;
}

.pic, .pic-chat, .pic-dash {
  border-radius: 25%;
  box-sizing: content-box;
  display: flex;
}

.pic-chat {
  max-height: 2em;
  max-width: 2em;
}

.pic-chat .initials {
  color: #FFFFFF;
  font-size: 1.5em;
}

.pic-dash {
  height: 4em;
  margin: 0.5em 1.25em 0 0.5em;
  width: 4em;
}

.input__search {
  background-color: #FFFFFF;
  border: 0.1em solid #C0C0C0;
  border-radius: 0.9em;
  font-size: 1rem;
  outline: none;
  padding: 0.3em;
  width: 11em;
}

.messages {
  flex: 1 1;
  margin: 0;
  overflow-y: auto;
  padding: 0.75em;
}

.messages__sent {
  display: flex;
  margin: 0 0 1em;
}

.user-data {
  align-items: baseline;
  display: inherit;
  line-height: 1.4em;
}

.user-data__username {
  font-size: 0.9em;
  text-transform: lowercase;
}

.user-data__hyperlink {
  font-size: 85%;
}

.user-data__copy {
  font-size: 51%;
}

.user-data__timestamp {
  color: #1FC2C2;
  font-size: 0.7em;
  margin: 0 0.5em;
}
